<template>

    <div class="cs-bjxq2-page" v-bind:style="mapStyle">

            <div class="cs-bjxq-hd"><span class="el-dialog__title">报警详情</span>
                <div class="el-dialog__headerbtn" @click="hideModel"><i class="el-dialog__close el-icon el-icon-close"></i></div>
            </div>
            <div class="cs-collapse-main">

                <el-collapse v-model="activeNames" >
                    <el-collapse-item title="报警人信息" name="1">
                        <div>报警人：{{jqfb_list_detail.alertUserRealname}}</div>
                        <div>电话：{{jqfb_list_detail.cellphone}}</div>
                        <div>报警时间：{{jqfb_list_detail.alertTime}}</div>
                    </el-collapse-item>
                    <el-collapse-item title="位置信息" name="2" >
                        <div id="current_position" class="cs-map-bjxq"></div>
                    </el-collapse-item>
                    <el-collapse-item title="警情信息" name="3">
                        <div>报警地点：{{jqfb_list_detail.address}}</div>
                        <div>报警内容：{{jqfb_list_detail.description}}</div>
                    </el-collapse-item>
                    <el-collapse-item title="图像信息" name="4">
                        <!--图片-->
                        <el-row :gutter="10">
                          <!-- <h5>图片</h5> -->
                          <el-col :span="6" v-if="n.url?true:false"  v-for="(n, index) in imageList" :data-index="index">
                            <img class="image" @click="open($event)" :src="n.url">
                          </el-col>
                        </el-row>
                        <!-- 音频 -->
                        <el-row>
                          <el-col v-for="(song, index) in songList"  v-if="song?true:false">
                            <audio ref="audios" controls="false">                                
                                <source :src="song">
                            </audio>
                          </el-col>
                        </el-row>
                        <!--视频-->
                        <el-row :gutter="10">
                           <!-- <h5>视频</h5> -->
                          <el-col :span="6" v-for="(video,index) in videoList" v-if="video?true:false"><span v-if="video?true:false" @click="playvideo(video)">视频{{1+index}}</span></el-col>
                        </el-row>
                    </el-collapse-item>

                    <el-collapse-item title="处理流程" name="5">
                        <div class="el-steps is-vertical" v-for="(log, index) in jqfb_list_detail.logs">
                            <div class="cs-box el-step is-vertical" style="margin-right: 0px; min-height: 100px;">
                                <div class="el-step__head is-finish is-text" style="width: 30px;">
                                    <div class="el-step__line is-vertical" style="margin-right: 0px;">
                                        <i class="el-step__line-inner" style="transition-delay: 0ms; height: 50%;"></i>
                                    </div>
                                    <span class="el-step__icon"><div>{{index+1}}</div></span>
                                </div>
                                <div class="el-step__main" style="margin-left: 0px;">
                                    <div class="el-step__title is-finish">{{log.createTime}}</div>
                                    <div class="el-step__description is-finish">
                                        <div>{{log.realname}}</div>
                                        <div>{{log.title}}</div>
                                        <div>{{log.description}}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </el-collapse-item>

                </el-collapse>

            </div>

            <div class="bjxq-dialog-footer">
                <button type="button" v-if="false" class="el-button el-button--default" @click="showsblb"><span>上报</span></button>
                <button type="button" v-if="false" class="el-button el-button--default" @click="showwplb"><span>委派</span></button>
                <button type="button" v-if="jqfb_list_detail.status == 0" class="el-button el-button--default" @click="showjcjb"><span>解除警报</span></button>
                <button type="button" v-if="false" class="el-button el-button--default"><span>视频通话</span></button>
            </div>


    </div>


</template>

<script>
import fancyBox from 'vue-fancybox';
import { videoPlayer } from 'vue-video-player';
export default {
    props: {
        jqfb_list_detail:{
            type:Object,
            default:null
        },
        mapHeight:{
            type:Number,
            default:600
        },
        isShow_bjxq :{
            type:Boolean,
            default:false
        }
    },
    components: {
        videoPlayer,
    },
    close: function() {
        console.log("myclose");
    },
    data: function () {
        return {
            dialogMainHeight:500,

            mapStyle:{
                width:'100%',
                height:this.mapHeight-80 +'px'
            },
            activeNames: ['1','2'],
            // details: {
            //     id:12321,
            //     username: '缍本',
            //     phone: '18571517269',
            //     create: '2016-07-05 12:05',
            //     addr: '湖北省武汉市洪山区珞瑜东路',
            //     content: "路中梢顺风耳",
            //     type: [],
            //     resource: '',
            //     desc: ''
            // },
            dialog_size:"large",
            formLabelWidth: '120px',
            imageList: [
            ],
            songList:[],
            videoList:[]
        }
    },
    methods: {
        hideModel(){
            this.$emit('hidden_bjxq');
        },
        showjcjb() {
            const Bus = global.eventHub;
            Bus.$emit('doto_show_jcjb',this.jqfb_list_detail.alertId,this.jqfb_list_detail.alertCategory,this.jqfb_list_detail.remark);
            //Bus.$emit('doto_show_jcjb',this.jqfb_list_detail.alertId,this.jqfb_list_detail.alertCategory,this.jqfb_list_detail.remark);
        },
        showsblb(){
            this.$emit('doto_showsblb');
        },
        showwplb(){
            this.$emit('doto_showwplb');
        },
        addMarker(point, src) {

            //创建地图上的标识物
            var that=this;
            let BMap = this.$BMap2;
            var convertor = new BMap.Convertor();
            var pointArr = [];
            pointArr.push(point);
            convertor.translate(pointArr, 3, 5, translateCallback);
            function translateCallback(data){
              if(data.status === 0) {
                let marker = new BMap.Marker(data.points[0], {icon: new BMap.Icon(src, new BMap.Size(21,25)) });
                let map2 = new BMap.Map("current_position");
                map2.centerAndZoom(data.points[0], 15);
                map2.enableScrollWheelZoom();
                that.map = map2;
                that.map.addOverlay(marker);
              }
            }
        },
        createJQMap() {
            let BMap = this.$BMap2;
            var local=this.jqfb_list_detail.location.split(",");
            this.addMarker(new BMap.Point(local[0],local[1]), "./src/img/icon/jingqing1.png");
        },
        initSmallMap () {

            this.$BMap2 = global.BMap;
            // console.log("==========");
            // console.log(this.$BMap2);
            this.createJQMap();

        },
        open (e) {
          fancyBox(e.target, this.imageList);
        },
        playvideo:function(video){
            const Bus = global.eventHub;
            // console.log(video)
            if(video){
                Bus.$emit('doto_show_jqshiping',video);
            }
        }
    },
    created: function () {
        // console.log("hehe"+this.jqfb_list_detail);
        this.dialogMainHeight = this.mapHeight - 100;
        this.dialogMainStyle={
            height:this.dialogMainHeight +'px'
        };
        // this.initSmallMap();
        //const {getSmallMapScript, initSmallMap} = this
        //getSmallMapScript().then(initSmallMap);
    },
    mounted:function() {
        // console.log("mounted");
        // console.log(this.jqfb_list_detail);
        this.initSmallMap();
        let that = this;
        let msg = this.jqfb_list_detail;
        let obj={width: 900, height: 675};
	    if(msg.imgs){
            var arr=msg.imgs.split(",");
            arr.forEach(function(value,index,array){
                that.imageList.push({width: 900, height: 675,url:value});
            });
        }
        if(msg.videos){
            that.videoList=msg.videos.split(",") ;
        }
        if(msg.voices){
            that.songList=msg.voices.split(",") ;
            console.log(that.songList)
        }

    },
    watch:{
        jqfb_list_detail:function(){
            this.initSmallMap();
            var imgarr=[];
            let that = this;
            let msg = this.jqfb_list_detail;
            let obj={width: 900, height: 675};
            if(msg.imgs){
                var arr=msg.imgs.split(",");
                arr.forEach(function(value,index,array){
                    imgarr.push({width: 900, height: 675,url:value});
                });
            }
            that.imageList=imgarr;
            if(msg.videos){
                that.videoList=msg.videos.split(",") ;
            }else{
                that.videoList=[];
            }
            if(msg.voices){
                that.songList=msg.voices.split(",") ;
                console.log(that.songList)
            }else{
                that.songList=[];
            }
        }
    }

}
</script>
<style>
.cs-bjxq-dailog {top:108px;right:0px;position:absolute;background:#fff;border-radius:2px;box-shadow:0 1px 3px rgba(0,0,0,.3);box-sizing:border-box;}
.cs--bjxq-dialog__footer {bottom:0px;position:absolute;right:20px;}
.cs-bjxq-dailog__body {margin-bottom:60px;overflow-x:hidden;overflow-y:auto}
.cs-bjxq-hd{padding: 0 0 20px;}
.cs-map-bjxq{width: 100%;height: 200px; }
.bjxq-dialog-footer{display: -moz-box; display: -webkit-box;  display: box;display: -moz-flex; display: -webkit-flex;display: flex;flex-direction: row;  flex-wrap: wrap;flex-flow: row wrap;padding: 20px 0 0;justify-content:flex-end;}
.image {width:100%;display:block;}
.fancy-box-image-wrapper {top:0;right:0;left:0;position:fixed;width:100%;height:100%;z-index:33}
.fancy-box-image-wrapper div {top:0;right:0;left:0;position:absolute;width:100%;height:100%;-webkit-transform-origin:left top 0;transform-origin:left top 0;-webkit-transition:-webkit-transform .3s cubic-bezier(0.4,0,0.22,1);transition:-webkit-transform .3s cubic-bezier(0.4,0,0.22,1);transition:transform .3s cubic-bezier(0.4,0,0.22,1);transition:transform .3s cubic-bezier(0.4,0,0.22,1),-webkit-transform .3s cubic-bezier(0.4,0,0.22,1)}
.fancy-box-image-wrapper img {max-width:100%;max-height:100%}
.fancy-box-mask {top:0;right:0;left:0;position:fixed;width:100%;height:100%;background-color:#333;z-index: 9999;}
.fancy-enter,.fancy-leave-active {opacity:0}
.fancy-enter-active,.fancy-leave-active {-webkit-transition:.2s;transition:.2s}

</style>


